  .t-news-btn {
    position: relative;
    padding: 0.75rem 3rem;
    min-width: 50%;
    margin-top: 2.5rem;
    font-size: 1.25rem;
    border-style: dotted;
    box-shadow: 0.5rem 0.5rem $grey-100;
  }

