      .t-news-label {
        position: absolute;
        right: .5rem;
        top: 50%;
        transform: translateY(-50%);
        width: 4rem;
        user-select: none;

        .new-icons {
          width: 4rem;
          height: 4rem;
        }
      }

      .t-news-date {
        background-color: $grey-100;
        border-radius: 1rem;
        color: $dark;
        padding: 0.375rem 1rem;
        margin-right: .5rem;
        margin-left: 1rem;
        user-select: none;
        white-space: nowrap;

        .date-year {
          font-size: .875rem;
          color: $grey-500;
        }

        .date-month {
          font-size: 1.25rem;
          font-weight: 500;
        }

        .date-day {
